body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  padding: 20px;
}

.new-product-name>div {
  width: -webkit-fill-available;
}

#hw1 .ui[class*="right labeled"].input>input.price-input {
  border-top-right-radius: .28571429rem !important;
  border-bottom-right-radius: .28571429rem !important;
  border-right-color: rgba(34,36,38,.15) !important;
}

#hw1 {
  .price-column {
    min-width: 240px;
    text-align: end;

    input {
      text-align: end;
    }
  }
}

.hw-description {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  border-radius: .28571429rem;
  background-color: #004282;
  color: #fff;
  padding: 10px;
  left: 10%;
  position: fixed;
  top: 10%;
  z-index: 1000; 
  
  p {
    margin: 0 0 0.5em !important;
    line-height: 1em;
  }
}

/* hw2 */
#hw2 {
  img {
    width: 100%;
  }
}